export default {
	data() {
		return {
			globalData: {},
			list: {},
			details:{}
		};
	},
	onLoad() {
		this.globalData = getApp().globalData;
		this.loadData();
	},
	methods: {
		loadData() {
			this.$minApi
				.wallet()
				.then(res => {
					this.details = res.data;
				})
				.catch(err => {
					console.log(err);
				});
			this.$minApi
				.walletRecord()
				.then(res => {
					this.list = res;
				})
				.catch(err => {
					console.log(err);
				});
		}
	}
};
